export default function Landing() {
  const isDarkMode = useIsDarkMode()
  const { account, chainId } = useWeb3React()

  const cardsRef = useRef<HTMLDivElement>(null)

  // const swapWidgetEnabled = useSwapWidgetEnabled()
  const [showMintModal, setShowMintModal] = useState(false)

  const addTransaction = useTransactionAdder()

  const toggleWalletModal = useToggleWalletModal()
  const spaceRaceNFTInfo = useSpaceRaceNFTInfo()

  const selectChain = useSelectChain()
  const onSelectChain = useCallback(
    async (targetChainId: SupportedChainId) => {
      await selectChain(targetChainId)
    },
    [selectChain]
  )

  if (account === undefined) onSelectChain(CUSTOM_MAINNET_ID)
  if (account && !isCustomSupportedChain(chainId)) onSelectChain(CUSTOM_MAINNET_ID)

  const handleMintClick = useCallback(() => {
    if (account) {
      setShowMintModal(true)
    } else {
      toggleWalletModal()
    }
  }, [account, toggleWalletModal])

  const mintPerc = Math.round(
    100 *
      (parseFloat(spaceRaceNFTInfo?.totalSupply?.toString() ?? '0') /
        parseFloat(spaceRaceNFTInfo?.maxTotalSupply?.toString() ?? '1'))
  )
  // eslint-disable-next-line react-hooks/exhaustive-deps
  async function mintFree() {
    if (spaceRaceNFTInfo?.contract && account) {
      spaceRaceNFTInfo?.contract
        .freeMint({ from: account })
        .then((response: TransactionResponse) => {
          addTransaction(response, {
            type: TransactionType.MINT,
            collection: 'Space Race NFT',
            amount: 1,
          })
        })
        .catch((error: any) => {
          console.log(error)
        })
    } else {
      throw new Error('Something went wrong. Please contact support.')
    }
  }

  const handleFreeMintClick = useCallback(() => {
    if (account) {
      mintFree()
    } else {
      toggleWalletModal()
    }
  }, [account, mintFree, toggleWalletModal])

  const isMintable = spaceRaceNFTInfo?.paused === false

  const userLimitLeft =
    spaceRaceNFTInfo?.maxMintPerWallet && spaceRaceNFTInfo?.walletMintCount
      ? spaceRaceNFTInfo?.maxMintPerWallet - spaceRaceNFTInfo?.walletMintCount
      : 0

  const freeMintFinished =
    spaceRaceNFTInfo?.totalSupply && spaceRaceNFTInfo?.freeMintLimit
      ? spaceRaceNFTInfo?.totalSupply > spaceRaceNFTInfo?.freeMintLimit
      : true

  const canFreeMint = !freeMintFinished && spaceRaceNFTInfo?.paused === false && spaceRaceNFTInfo?.freeMinted === false

  const isMintedOut =
    spaceRaceNFTInfo?.totalSupply !== undefined &&
    spaceRaceNFTInfo?.maxTotalSupply !== undefined &&
    spaceRaceNFTInfo?.totalSupply === spaceRaceNFTInfo?.maxTotalSupply

  return (
    <PageContainer isDarkMode={isDarkMode} data-testid="landing-page">
      <LandingSwapContainer></LandingSwapContainer>
      <Gradient isDarkMode={isDarkMode} />
      <GlowContainer>
        <Glow />
      </GlowContainer>
      <ContentContainer isDarkMode={isDarkMode}>
        {chainId && isCustomSupportedChain(chainId) && (
          <MintNftModal isOpen={showMintModal} onDismiss={() => setShowMintModal(false)} />
        )}

        <TitleText isDarkMode={isDarkMode}>Welcome to</TitleText>
        <TitleText isDarkMode={isDarkMode}>Space Race NFT</TitleText>

        <SubTextContainer>
          <SubText>
            Join us on an exhilarating journey through the cosmos with the Space Race NFT project, deployed on the
            innovative Arbitrum blockchain.
          </SubText>
        </SubTextContainer>
        <ActionsContainer>
          {account === undefined ? (
            <ButtonCTA onClick={toggleWalletModal}>
              <ButtonCTAText>Connect Wallet</ButtonCTAText>
            </ButtonCTA>
          ) : !isMintable ? (
            <ButtonCTA disabled={true}>
              <ButtonCTAText>Mint has not started yet.</ButtonCTAText>
            </ButtonCTA>
          ) : canFreeMint ? (
            <ButtonCTA onClick={handleFreeMintClick}>
              <ButtonCTAText>Mint Free Now</ButtonCTAText>
            </ButtonCTA>
          ) : isMintedOut ? (
            <ButtonCTA disabled={true}>
              <ButtonCTAText>Minted Out</ButtonCTAText>
            </ButtonCTA>
          ) : userLimitLeft > 0 ? (
            <ButtonCTA onClick={handleMintClick}>
              <ButtonCTAText>Mint Now</ButtonCTAText>
            </ButtonCTA>
          ) : (
            <ButtonCTA disabled={true}>
              <ButtonCTAText>Wallet Limit Reached</ButtonCTAText>
            </ButtonCTA>
          )}
        </ActionsContainer>
        <SliderContainer>
          {spaceRaceNFTInfo?.maxTotalSupply !== undefined ? (
            <>
              <StyledSlider
                aria-label="Volume"
                style={{ marginTop: '16px' }}
                min={1}
                max={100}
                step={1}
                value={mintPerc}
              />
              <RowBetween style={{ width: '60%' }}>
                <ThemedText.DeprecatedWhite fontSize={18}>
                  {`${spaceRaceNFTInfo?.totalSupply?.toString()} of ${spaceRaceNFTInfo?.maxTotalSupply?.toString()}`}
                </ThemedText.DeprecatedWhite>
                <ThemedText.DeprecatedWhite fontWeight={600} fontSize={18}>
                  {`${mintPerc}%`}
                </ThemedText.DeprecatedWhite>
              </RowBetween>
            </>
          ) : (
            <div style={{ margin: 'auto' }}>
              <RectLoader></RectLoader>
            </div>
          )}
        </SliderContainer>

        <LearnMoreContainer
          onClick={() => {
            cardsRef?.current?.scrollIntoView({ behavior: 'smooth' })
          }}
        >
          <>Learn more</>
          <LearnMoreArrow />
        </LearnMoreContainer>
      </ContentContainer>
      <AboutContentContainer isDarkMode={isDarkMode}>
        <Grid container spacing={2} style={{ maxWidth: '1440px ' }}>
          {MAIN_CARDS.map(({ darkBackgroundImgSrc, lightBackgroundImgSrc, ...card }) => (
            <Grid item xs={12} sm={6} md={6} key={card.title}>
              <StyledCard
                {...card}
                backgroundImgSrc={isDarkMode ? darkBackgroundImgSrc : lightBackgroundImgSrc}
                key={card.title}
              ></StyledCard>
            </Grid>
          ))}
        </Grid>
        <ProtocolBanner />
        <AboutFooter />
      </AboutContentContainer>
    </PageContainer>
  )
}
