import { CurrencyAmount, SupportedChainId, Token } from '@uniswap/sdk-core'
import { useWeb3React } from '@web3-react/core'
import { isCustomSupportedChain } from 'constants/chains'
import { TEST_SPACE_RACE_NFT } from 'constants/nftProjects'
import { Contract } from 'ethers/lib/ethers'
import { useSpaceRaceNFTContract } from 'hooks/useContract'
import JSBI from 'jsbi'
import { useSingleCallResult } from 'lib/hooks/multicall'
import { useMemo } from 'react'

import { nativeOnChain } from '../../constants/tokens'

const SPACE_RACE_NFT_INFO: {
  [chainId: number]: {
    collection: Token
  }
} = {
  [SupportedChainId.POLYGON_MUMBAI]: {
    collection: TEST_SPACE_RACE_NFT,
  },
  [SupportedChainId.ARBITRUM_ONE]: {
    collection: TEST_SPACE_RACE_NFT,
  },
}

interface SpaceRaceNFTInfo {
  token: Token | undefined
  contract: Contract | null
  balanceOf: number | undefined
  walletMintCount: number | undefined
  totalSupply: number | undefined
  paused: boolean | undefined
  freeMinted: boolean | undefined
  isTeamMember: boolean | undefined
  mintPrice: CurrencyAmount<Token> | undefined
  maxTotalSupply: number | undefined
  maxMintPerWallet: number | undefined
  freeMintLimit: number | undefined
}

// gets the staking info from the network for the active chain id
// eslint-disable-next-line import/no-unused-modules
export function useSpaceRaceNFTInfo(): SpaceRaceNFTInfo {
  const { chainId, account } = useWeb3React()
  const isSupportedChain = isCustomSupportedChain(chainId)
  const contractAddress = useMemo(
    () => (isSupportedChain && chainId ? SPACE_RACE_NFT_INFO[chainId].collection.address : undefined),
    [chainId, isSupportedChain]
  )
  const native = isSupportedChain && chainId ? nativeOnChain(chainId) : undefined

  const accountArg = useMemo(() => [account ?? undefined], [account])
  const contract = useSpaceRaceNFTContract(contractAddress)
  const balanceOf = useSingleCallResult(contract, 'balanceOf', accountArg)
  const walletMintCount = useSingleCallResult(contract, 'walletMintCount', accountArg)
  const freeMinted = useSingleCallResult(contract, 'freeMinted', accountArg)
  const isTeamMember = useSingleCallResult(contract, 'isTeamMember', accountArg)

  const totalSupply = useSingleCallResult(contract, 'totalSupply')
  const paused = useSingleCallResult(contract, 'paused')
  const mintPrice = useSingleCallResult(contract, 'mintPrice')
  const maxTotalSupply = useSingleCallResult(contract, 'maxSupply')
  const maxMintPerWallet = useSingleCallResult(contract, 'maxMintPerWallet')
  const freeMintLimit = useSingleCallResult(contract, 'freeMintLimit')

  return useMemo(() => {
    const memo: SpaceRaceNFTInfo = {
      token: undefined,
      contract,
      balanceOf: undefined,
      walletMintCount: undefined,
      totalSupply: undefined,
      paused: undefined,
      freeMinted: undefined,
      isTeamMember: undefined,
      mintPrice: undefined,
      maxTotalSupply: undefined,
      maxMintPerWallet: undefined,
      freeMintLimit: undefined,
    }
    if (!isSupportedChain) return memo
    else if (!chainId) return memo
    else {
      if (
        !balanceOf?.loading &&
        !totalSupply?.loading &&
        !paused?.loading &&
        !freeMinted?.loading &&
        !isTeamMember?.loading &&
        !mintPrice?.loading &&
        !maxTotalSupply?.loading &&
        !maxMintPerWallet?.loading &&
        !walletMintCount?.loading &&
        !freeMintLimit?.loading
      ) {
        if (
          balanceOf?.error ||
          totalSupply?.error ||
          paused?.error ||
          freeMinted?.error ||
          isTeamMember?.error ||
          mintPrice?.error ||
          maxTotalSupply?.error ||
          maxMintPerWallet?.error ||
          walletMintCount?.error ||
          freeMintLimit?.error
        ) {
          console.error('Failed to load Space Race NFT info')
          return memo
        }
        memo.balanceOf = parseInt(balanceOf?.result?.[0].toString())
        memo.walletMintCount = parseInt(walletMintCount?.result?.[0].toString())

        memo.totalSupply = parseInt(totalSupply?.result?.[0].toString())
        memo.paused = paused?.result?.[0]
        memo.freeMinted = freeMinted?.result?.[0]
        memo.isTeamMember = isTeamMember?.result?.[0]

        memo.mintPrice = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(mintPrice?.result?.[0] ?? 0))
          : undefined

        memo.maxTotalSupply = parseInt(maxTotalSupply?.result?.[0].toString())
        memo.maxMintPerWallet = parseInt(maxMintPerWallet?.result?.[0].toString())
        memo.freeMintLimit = parseInt(freeMintLimit?.result?.[0].toString())

        memo.token = chainId ? SPACE_RACE_NFT_INFO[chainId].collection : undefined
        return memo
      }
      return memo
    }
  }, [
    balanceOf?.error,
    balanceOf?.loading,
    balanceOf?.result,
    chainId,
    contract,
    freeMintLimit?.error,
    freeMintLimit?.loading,
    freeMintLimit?.result,
    freeMinted?.error,
    freeMinted?.loading,
    freeMinted?.result,
    isSupportedChain,
    isTeamMember?.error,
    isTeamMember?.loading,
    isTeamMember?.result,
    maxMintPerWallet?.error,
    maxMintPerWallet?.loading,
    maxMintPerWallet?.result,
    maxTotalSupply?.error,
    maxTotalSupply?.loading,
    maxTotalSupply?.result,
    mintPrice?.error,
    mintPrice?.loading,
    mintPrice?.result,
    native,
    paused?.error,
    paused?.loading,
    paused?.result,
    totalSupply?.error,
    totalSupply?.loading,
    totalSupply?.result,
    walletMintCount?.error,
    walletMintCount?.loading,
    walletMintCount?.result,
  ])
}
