import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import ListItemButton from '@mui/material/ListItemButton'
import ListItemIcon from '@mui/material/ListItemIcon'
import ListItemText from '@mui/material/ListItemText'
import { nativeOnChain } from '@uniswap/smart-order-router'
import { useWeb3React } from '@web3-react/core'
import { Break } from 'components/earn/styled'
import { RowBetween } from 'components/Row'
import * as React from 'react'
import { useSpaceRaceMarsInfo } from 'state/spaceRace/spaceRaceMars'
import styled from 'styled-components/macro'
import { ThemedText } from 'theme'

import StarIcon from '../../assets/images/star.png'
import { ContentWrapper, CustomBox } from './style'

const StyledText = styled(ListItemText)`
  .MuiTypography-root {
    font-size: 14px;
    font-weight: 400;
    color: #ddd;
    text-shadow: 2px 2px #000a;
    font-family: 'Minecraftia';
  }
`
export default function Stat() {
  const { chainId } = useWeb3React()

  const marsInfo = useSpaceRaceMarsInfo()
  const native = chainId ? nativeOnChain(chainId) : undefined

  return (
    <ContentWrapper>
      <RowBetween>
        <ThemedText.MediumHeader style={{ margin: '0px auto 16px' }}>Info Table</ThemedText.MediumHeader>
      </RowBetween>
      <CustomBox>
        <List style={{ paddingRight: '25px' }}>
          <ListItem
            disablePadding
            secondaryAction={
              <ThemedText.SubHeaderSmall
                style={{ margin: 'auto' }}
              >{`${marsInfo?.totalSupply?.toString()}/${marsInfo?.maxTotalSupply?.toString()}`}</ThemedText.SubHeaderSmall>
            }
          >
            <ListItemButton>
              <ListItemIcon style={{ minWidth: '30px' }}>
                <img src={StarIcon} alt="star" style={{ width: '20px' }} />
              </ListItemIcon>
              <StyledText primary="Martians" />
            </ListItemButton>
          </ListItem>

          <ListItem
            disablePadding
            secondaryAction={
              <ThemedText.SubHeaderSmall style={{ margin: 'auto' }}>{`${marsInfo?.totalDistributed?.toFixed(4)} ${
                native?.wrapped?.symbol
              }`}</ThemedText.SubHeaderSmall>
            }
          >
            <ListItemButton>
              <ListItemIcon style={{ minWidth: '30px' }}>
                <img src={StarIcon} alt="star" style={{ width: '20px' }} />
              </ListItemIcon>
              <StyledText primary="Total Distributed" />
            </ListItemButton>
          </ListItem>

          <ListItem
            disablePadding
            secondaryAction={
              <ThemedText.SubHeaderSmall style={{ margin: 'auto' }}>{`${marsInfo?.totalWithdrawn?.toFixed(4)} ${
                native?.wrapped?.symbol
              }`}</ThemedText.SubHeaderSmall>
            }
          >
            <ListItemButton>
              <ListItemIcon style={{ minWidth: '30px' }}>
                <img src={StarIcon} alt="star" style={{ width: '20px' }} />
              </ListItemIcon>
              <StyledText primary="Total Withdrawn" />
            </ListItemButton>
          </ListItem>
          {marsInfo?.totalClaimed?.greaterThan('0') && (
            <ListItem
              disablePadding
              secondaryAction={
                <ThemedText.SubHeaderSmall style={{ margin: 'auto' }}>{`${marsInfo?.totalClaimed?.toFixed(4)} ${
                  native?.wrapped?.symbol
                }`}</ThemedText.SubHeaderSmall>
              }
            >
              <ListItemButton>
                <ListItemIcon style={{ minWidth: '30px' }}>
                  <img src={StarIcon} alt="star" style={{ width: '20px' }} />
                </ListItemIcon>
                <StyledText primary="Total Claimed Reward" />
              </ListItemButton>
            </ListItem>
          )}

          <Break></Break>

          <ListItem
            disablePadding
            secondaryAction={
              <ThemedText.SubHeaderSmall
                style={{ margin: 'auto' }}
              >{`${marsInfo?.blockLeftDiscount} Blocks`}</ThemedText.SubHeaderSmall>
            }
          >
            <ListItemButton>
              <ListItemIcon style={{ minWidth: '30px' }}>
                <img src={StarIcon} alt="star" style={{ width: '20px' }} />
              </ListItemIcon>
              {marsInfo?.discount ? (
                <StyledText primary="Discount will finish in" />
              ) : (
                <StyledText primary="Discount will start in" />
              )}
            </ListItemButton>
          </ListItem>

          <ListItem
            disablePadding
            secondaryAction={
              <ThemedText.SubHeaderSmall style={{ margin: 'auto' }}>{`${marsInfo?.returnFee?.toFixed(
                6
              )} `}</ThemedText.SubHeaderSmall>
            }
          >
            <ListItemButton>
              <ListItemIcon style={{ minWidth: '30px' }}>
                <img src={StarIcon} alt="star" style={{ width: '20px' }} />
              </ListItemIcon>
              <StyledText primary="Return Fee" />
            </ListItemButton>
          </ListItem>

          <ListItem
            disablePadding
            secondaryAction={
              <ThemedText.SubHeaderSmall style={{ margin: 'auto' }}>{`${marsInfo?.goFee?.toFixed(
                6
              )} `}</ThemedText.SubHeaderSmall>
            }
          >
            <ListItemButton>
              <ListItemIcon style={{ minWidth: '30px' }}>
                <img src={StarIcon} alt="star" style={{ width: '20px' }} />
              </ListItemIcon>
              <StyledText primary="Go Fee" />
            </ListItemButton>
          </ListItem>

          {marsInfo?.balanceOf !== undefined && parseInt(marsInfo?.balanceOf?.toString()) > 0 && (
            <>
              <Break></Break>
              <ListItem
                disablePadding
                secondaryAction={
                  <ThemedText.SubHeaderSmall
                    style={{ margin: 'auto' }}
                  >{`${marsInfo?.balanceOf?.toString()} `}</ThemedText.SubHeaderSmall>
                }
              >
                <ListItemButton>
                  <ListItemIcon style={{ minWidth: '30px' }}>
                    <img src={StarIcon} alt="star" style={{ width: '20px' }} />
                  </ListItemIcon>
                  <StyledText primary="Your Racers at Mars" />
                </ListItemButton>
              </ListItem>

              <ListItem
                disablePadding
                secondaryAction={
                  <ThemedText.SubHeaderSmall style={{ margin: 'auto' }}>{`${marsInfo?.withdrawableDividend?.toFixed(
                    4
                  )} ${native?.wrapped?.symbol}`}</ThemedText.SubHeaderSmall>
                }
              >
                <ListItemButton>
                  <ListItemIcon style={{ minWidth: '30px' }}>
                    <img src={StarIcon} alt="star" style={{ width: '20px' }} />
                  </ListItemIcon>
                  <StyledText primary="Withdrawable Dividend" />
                </ListItemButton>
              </ListItem>

              <ListItem
                disablePadding
                secondaryAction={
                  <ThemedText.SubHeaderSmall style={{ margin: 'auto' }}>{`${marsInfo?.withdrawableDividend
                    ?.divide(marsInfo?.balanceOf?.toString() ?? '1')
                    .toFixed(4)} ${native?.wrapped?.symbol}`}</ThemedText.SubHeaderSmall>
                }
              >
                <ListItemButton>
                  <ListItemIcon style={{ minWidth: '30px' }}>
                    <img src={StarIcon} alt="star" style={{ width: '20px' }} />
                  </ListItemIcon>
                  <StyledText primary="Income Per Racer" />
                </ListItemButton>
              </ListItem>
            </>
          )}
        </List>
      </CustomBox>
    </ContentWrapper>
  )
}
