import { CurrencyAmount, SupportedChainId, Token } from '@uniswap/sdk-core'
import { nativeOnChain } from '@uniswap/smart-order-router'
import { useWeb3React } from '@web3-react/core'
import { TEST_SPACE_RACE_MARS_NFT } from 'constants/nftProjects'
import { Contract } from 'ethers/lib/ethers'
import { useSpaceRaceMarsNFTContract, useSpaceRaceMarsTicketContract } from 'hooks/useContract'
import JSBI from 'jsbi'
import { useSingleCallResult } from 'lib/hooks/multicall'
import { useMemo } from 'react'

const SPACE_RACE_MARS_NFT_INFO: {
  [chainId: number]: {
    collection: Token
  }
} = {
  [SupportedChainId.POLYGON_MUMBAI]: {
    collection: TEST_SPACE_RACE_MARS_NFT,
  },
}

const SPACE_RACE_MARS_TICKET_INFO: {
  [chainId: number]: {
    address: string
  }
} = {
  [SupportedChainId.POLYGON_MUMBAI]: {
    address: '0x9f1607d045e5a674476458FC3a49F01396897Ed1',
  },
}

interface SpaceRaceMarsInfo {
  token: Token | undefined
  nftContract: Contract | null
  ticketContract: Contract | null
  balanceOf: number | undefined
  totalSupply: number | undefined
  maxTotalSupply: number | undefined
  withdrawableDividend: CurrencyAmount<Token> | undefined
  totalClaimed: CurrencyAmount<Token> | undefined
  totalDistributed: CurrencyAmount<Token> | undefined
  totalWithdrawn: CurrencyAmount<Token> | undefined
  goFee: CurrencyAmount<Token> | undefined
  returnFee: CurrencyAmount<Token> | undefined
  teamGoCount: number | undefined
  teamGoLimit: number | undefined
  rotationInBlocks: number | undefined
  ezReturnBlocks: number | undefined
  currentBlock: number | undefined
  blockLeftDiscount: number | undefined
  discount: boolean | undefined
  ticketPaused: boolean | undefined
}

export function useSpaceRaceMarsInfo(): SpaceRaceMarsInfo {
  const { chainId, account, provider } = useWeb3React()
  const nftContractAddress = useMemo(
    () => (chainId ? SPACE_RACE_MARS_NFT_INFO[chainId].collection.address : undefined),
    [chainId]
  )

  const ticketContractAddress = useMemo(
    () => (chainId ? SPACE_RACE_MARS_TICKET_INFO[chainId].address : undefined),
    [chainId]
  )
  const native = chainId ? nativeOnChain(chainId) : undefined

  const accountArg = useMemo(() => [account ?? undefined], [account])
  const nftContract = useSpaceRaceMarsNFTContract(nftContractAddress)
  const ticketContract = useSpaceRaceMarsTicketContract(ticketContractAddress)

  const balanceOf = useSingleCallResult(nftContract, 'balanceOf', accountArg)
  const totalSupply = useSingleCallResult(nftContract, 'totalSupply')
  const maxTotalSupply = useSingleCallResult(nftContract, 'maxSupply')

  const withdrawableDividend = useSingleCallResult(nftContract, 'withdrawableDividendOf', [account ?? undefined])
  const totalClaimed = useSingleCallResult(nftContract, 'withdrawnDividendOf', [account ?? undefined])
  const totalDistributed = useSingleCallResult(nftContract, 'totalDistributed')
  const totalWithdrawn = useSingleCallResult(nftContract, 'totalWithdrawn')

  const ticketPaused = useSingleCallResult(ticketContract, 'paused')

  const goFee = useSingleCallResult(ticketContract, 'goFee')
  const returnFee = useSingleCallResult(ticketContract, 'getReturnFee')

  const teamGoCount = useSingleCallResult(ticketContract, 'teamGoCount', [account ?? undefined])
  const teamGoLimit = useSingleCallResult(ticketContract, 'teamGoLimit')

  const rotationInBlocks = useSingleCallResult(ticketContract, 'rotationInBlocks')
  const ezReturnBlocks = useSingleCallResult(ticketContract, 'ezReturnBlocks')
  const currentBlock = provider?.blockNumber ?? undefined

  return useMemo(() => {
    const memo: SpaceRaceMarsInfo = {
      token: chainId ? SPACE_RACE_MARS_NFT_INFO[chainId].collection : undefined,
      nftContract,
      ticketContract,
      balanceOf: undefined,
      totalSupply: undefined,
      maxTotalSupply: undefined,
      withdrawableDividend: undefined,
      totalClaimed: undefined,
      totalDistributed: undefined,
      totalWithdrawn: undefined,
      goFee: undefined,
      returnFee: undefined,
      teamGoCount: undefined,
      teamGoLimit: undefined,
      rotationInBlocks: undefined,
      ezReturnBlocks: undefined,
      currentBlock,
      blockLeftDiscount: undefined,
      discount: undefined,
      ticketPaused: undefined,
    }

    if (!chainId) return memo
    else {
      if (
        !balanceOf?.loading &&
        !totalSupply?.loading &&
        !maxTotalSupply?.loading &&
        !withdrawableDividend?.loading &&
        !totalClaimed?.loading &&
        !totalDistributed?.loading &&
        !totalWithdrawn?.loading &&
        !goFee?.loading &&
        !returnFee?.loading &&
        !teamGoCount?.loading &&
        !teamGoLimit?.loading &&
        !rotationInBlocks?.loading &&
        !ezReturnBlocks?.loading &&
        !ticketPaused?.loading
      ) {
        if (
          balanceOf?.error ||
          totalSupply?.error ||
          maxTotalSupply?.error ||
          withdrawableDividend?.error ||
          totalClaimed?.error ||
          totalDistributed?.error ||
          totalWithdrawn?.error ||
          goFee?.error ||
          returnFee?.error ||
          teamGoCount?.error ||
          teamGoLimit?.error ||
          ticketPaused?.error
        ) {
          console.error('Failed to load Martian info')
          return memo
        }
        memo.balanceOf = parseInt(balanceOf?.result?.[0].toString())
        memo.totalSupply = parseInt(totalSupply?.result?.[0].toString())
        memo.maxTotalSupply = parseInt(maxTotalSupply?.result?.[0].toString())
        memo.withdrawableDividend = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(withdrawableDividend?.result?.[0] ?? 0))
          : undefined
        memo.totalClaimed = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(totalClaimed?.result?.[0] ?? 0))
          : undefined
        memo.totalDistributed = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(totalDistributed?.result?.[0] ?? 0))
          : undefined
        memo.totalWithdrawn = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(totalWithdrawn?.result?.[0] ?? 0))
          : undefined

        memo.goFee = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(goFee?.result?.[0] ?? 0))
          : undefined

        memo.returnFee = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(returnFee?.result?.[0] ?? 0))
          : undefined

        memo.teamGoCount = parseInt(teamGoCount?.result?.[0].toString())
        memo.teamGoLimit = parseInt(teamGoLimit?.result?.[0].toString())

        memo.rotationInBlocks = parseInt(rotationInBlocks?.result?.[0].toString())
        memo.ezReturnBlocks = parseInt(ezReturnBlocks?.result?.[0].toString())
        if (memo.currentBlock) {
          // in discount now
          if (memo.currentBlock % memo.rotationInBlocks <= memo.ezReturnBlocks) {
            memo.discount = true
            memo.blockLeftDiscount = memo.ezReturnBlocks - (memo.currentBlock % memo.rotationInBlocks)
          } else {
            memo.discount = false
            memo.blockLeftDiscount = memo.rotationInBlocks - (memo?.currentBlock % memo.rotationInBlocks)
          }
        }
        memo.ticketPaused = ticketPaused?.result?.[0]

        return memo
      }
      return memo
    }
  }, [
    chainId,
    nftContract,
    ticketContract,
    currentBlock,
    balanceOf?.loading,
    balanceOf?.error,
    balanceOf?.result,
    totalSupply?.loading,
    totalSupply?.error,
    totalSupply?.result,
    maxTotalSupply?.loading,
    maxTotalSupply?.error,
    maxTotalSupply?.result,
    withdrawableDividend?.loading,
    withdrawableDividend?.error,
    withdrawableDividend?.result,
    totalClaimed?.loading,
    totalClaimed?.error,
    totalClaimed?.result,
    totalDistributed?.loading,
    totalDistributed?.error,
    totalDistributed?.result,
    totalWithdrawn?.loading,
    totalWithdrawn?.error,
    totalWithdrawn?.result,
    goFee?.loading,
    goFee?.error,
    goFee?.result,
    returnFee?.loading,
    returnFee?.error,
    returnFee?.result,
    teamGoCount?.loading,
    teamGoCount?.error,
    teamGoCount?.result,
    teamGoLimit?.loading,
    teamGoLimit?.error,
    teamGoLimit?.result,
    rotationInBlocks?.loading,
    rotationInBlocks?.result,
    ezReturnBlocks?.loading,
    ezReturnBlocks?.result,
    ticketPaused?.loading,
    ticketPaused?.error,
    ticketPaused?.result,
    native,
  ])
}
