import { TransactionResponse } from '@ethersproject/providers'
import { Token } from '@uniswap/sdk-core'
import { useWeb3React } from '@web3-react/core'
import { nativeOnChain } from 'constants/tokens'
import { ApprovalState, useNFTApprovalStatus } from 'hooks/useApproveCallback'
import { useNativeCurrencyBalances } from 'lib/hooks/useCurrencyBalance'
import React, { useEffect, useState } from 'react'
import { useSpaceRaceMarsInfo } from 'state/spaceRace/spaceRaceMars'
import { useSpaceRaceNFTInfo } from 'state/spaceRace/spaceRaceNFT'
import { TransactionType } from 'state/transactions/types'
import styled from 'styled-components/macro'

import { useTransactionAdder } from '../../state/transactions/hooks'
import { CloseIcon, ThemedText } from '../../theme'
import { ButtonConfirmed, ButtonError } from '../Button'
import { AutoColumn } from '../Column'
import Modal from '../Modal'
import { LoadingView, SubmittedView } from '../ModalViews'
import { RowBetween } from '../Row'

const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 1rem;
`

interface BackToSpaceModalProps {
  isOpen: boolean
  onDismiss: () => void
  tokenIDs: string[]
  astronautsCollection: Token | undefined
  clearNftList(): any
}

export default function BackToEarthModal({
  isOpen,
  onDismiss,
  tokenIDs,
  astronautsCollection,
  clearNftList,
}: BackToSpaceModalProps) {
  const { account, chainId } = useWeb3React()

  const marsInfo = useSpaceRaceMarsInfo()
  const spaceRaceNFTInfo = useSpaceRaceNFTInfo()
  const userNativeBalance =
    useNativeCurrencyBalances(account ? [account] : [])?.[account ?? '']?.toSignificant(6) ?? '0'
  const native = chainId ? nativeOnChain(chainId) : undefined

  // monitor call to help UI loading state
  const [hash, setHash] = useState<string | undefined>()
  const [attempting, setAttempting] = useState(false)

  const totalReturnFee = marsInfo?.returnFee?.multiply(tokenIDs.length)

  const addTransaction = useTransactionAdder()
  const approvalState = useNFTApprovalStatus(marsInfo?.token, marsInfo?.ticketContract?.address)
  const [approvalPending, setApprovalPending] = useState<boolean>(false)
  const [approvalSubmitted, setApprovalSubmitted] = useState<boolean>(false)
  const approveTokenButtonDisabled =
    approvalState !== ApprovalState.NOT_APPROVED || approvalSubmitted || approvalPending

  function wrappedOnDismiss() {
    setHash(undefined)
    setAttempting(false)
    onDismiss()
  }

  // const mutateFee = BigNumber.from(Math.trunc(0.001 * Math.pow(10, 18))).toString()

  async function onReturn() {
    setAttempting(true)
    if (totalReturnFee && marsInfo?.ticketContract) {
      if (approvalState === ApprovalState.APPROVED && tokenIDs.length !== 0) {
        const totalReturnFeeBig = (Math.pow(10, 18) * parseFloat(totalReturnFee?.toExact())).toString()

        const numberSelectedNFT = tokenIDs.map((i) => Number(i))
        marsInfo.ticketContract
          .returnFromRace(numberSelectedNFT, { value: totalReturnFeeBig })
          .then((response: TransactionResponse) => {
            addTransaction(response, { type: TransactionType.MUTATE, collection: 'qqw' })
            setHash(response.hash)
            clearNftList()
          })
          .catch((error: any) => {
            setAttempting(false)
            console.log(error)
          })
      } else {
        setAttempting(false)
        throw new Error('Attempting to stake without approval or a signature. Please contact support.')
      }
    }
  }

  let error: string | undefined
  if (!account) {
    error = 'Connect Wallet'
  }
  if (totalReturnFee && parseFloat(totalReturnFee?.toSignificant(6)) > parseFloat(userNativeBalance)) {
    error = 'Insufficient Balance'
  }

  // mark when a user has submitted an approval, reset onTokenSelection for input field
  useEffect(() => {
    if (approvalState === ApprovalState.PENDING) {
      setApprovalSubmitted(true)
    }
  }, [approvalState, approvalSubmitted])

  async function onAttemptToApprove() {
    if (
      marsInfo?.nftContract &&
      marsInfo?.ticketContract &&
      marsInfo?.nftContract?.address &&
      marsInfo?.ticketContract?.address
    ) {
      setApprovalPending(true)
      marsInfo?.nftContract
        .setApprovalForAll(marsInfo?.ticketContract.address, true)
        .then((response: TransactionResponse) => {
          addTransaction(response, {
            type: TransactionType.APPROVAL,
            tokenAddress: spaceRaceNFTInfo?.contract?.address ?? '',
            spender: marsInfo?.ticketContract?.address ?? '',
          })
        })
        .catch((error: any) => {
          setApprovalSubmitted(false)
          setApprovalPending(false)
          console.log(error)
        })
    } else {
      throw new Error('Attempting to stake without approval or a signature. Please contact support.')
    }
  }
  return (
    <Modal isOpen={isOpen} onDismiss={wrappedOnDismiss} maxHeight={90}>
      {!attempting && !hash && (
        <ContentWrapper gap="lg">
          <RowBetween>
            <ThemedText.MediumHeader>Back to Earth</ThemedText.MediumHeader>
            <CloseIcon onClick={wrappedOnDismiss} />
          </RowBetween>

          <AutoColumn justify="center" gap="md">
            <ThemedText.SubHeader>Revert </ThemedText.SubHeader>

            <ThemedText.SubHeader fontWeight={600} fontSize={36}>
              {tokenIDs.join(', ')}
            </ThemedText.SubHeader>
            <ThemedText.SubHeader>{astronautsCollection?.name}</ThemedText.SubHeader>

            <RowBetween>
              <ThemedText.MediumHeader style={{ margin: 'auto' }}>Fee:</ThemedText.MediumHeader>
              <ThemedText.MediumHeader style={{ margin: 'auto' }}>{`${totalReturnFee?.toSignificant(6)} ${
                native?.symbol
              }`}</ThemedText.MediumHeader>
            </RowBetween>
          </AutoColumn>

          <RowBetween>
            <ButtonConfirmed
              mr="0.5rem"
              onClick={onAttemptToApprove}
              disabled={approveTokenButtonDisabled}
              confirmed={approvalState === ApprovalState.APPROVED}
              altDisabledStyle={approvalState === ApprovalState.PENDING} // show solid button while waiting
            >
              Approve
            </ButtonConfirmed>
            <ButtonError
              disabled={approvalState !== ApprovalState.APPROVED || !!error}
              error={!!error}
              onClick={onReturn}
            >
              {error ?? 'Revert'}
            </ButtonError>
          </RowBetween>
        </ContentWrapper>
      )}
      {attempting && !hash && (
        <LoadingView onDismiss={wrappedOnDismiss}>
          <AutoColumn gap="12px" justify="center">
            <ThemedText.BodyPrimary fontSize={20}>Returning...</ThemedText.BodyPrimary>
          </AutoColumn>
        </LoadingView>
      )}
      {hash && (
        <SubmittedView onDismiss={wrappedOnDismiss} hash={hash}>
          <AutoColumn gap="12px" justify="center">
            <ThemedText.LargeHeader>Transaction Submitted</ThemedText.LargeHeader>
            <ThemedText.BodyPrimary fontSize={20}>Returned!</ThemedText.BodyPrimary>
          </AutoColumn>
        </SubmittedView>
      )}
    </Modal>
  )
}
