import Grid from '@mui/material/Grid'
import { Token } from '@uniswap/sdk-core'
import { SupportedChainId } from '@uniswap/sdk-core'
import { useWeb3React } from '@web3-react/core'
import Loader from 'components/Loader'
import NFTItem from 'components/SpaceRace/NFTItem'
import { ContentWrapper, StyledFormControl, Styles } from 'components/SpaceRace/style'
import { TEST_SPACE_RACE_NFT } from 'constants/nftProjects'
import React, { useCallback } from 'react'
import { useTokenIDs } from 'state/spaceRace/portfolio'
import { useSpaceRaceNFTInfo } from 'state/spaceRace/spaceRaceNFT'

import { CloseIcon, ThemedText } from '../../theme'
import Modal from '../Modal'
import { RowBetween } from '../Row'

const SPACE_RACE_NFT_COLLECTIONS: {
  [chainId: number]: Token
} = {
  [SupportedChainId.POLYGON_MUMBAI]: TEST_SPACE_RACE_NFT,
}

interface MintModalProps {
  isOpen: boolean
  onDismiss: () => void
}

export default function ListNFTModal({ isOpen, onDismiss }: MintModalProps) {
  const { chainId } = useWeb3React()
  const spaceRaceCollection = chainId ? SPACE_RACE_NFT_COLLECTIONS[chainId] : undefined
  const spaceRaceInfo = useSpaceRaceNFTInfo()
  const spaceRaceTokenIDs = useTokenIDs(spaceRaceCollection)
  const [selectedNFTList] = React.useState<string[]>([])
  const isLoaded = spaceRaceCollection && spaceRaceInfo && spaceRaceTokenIDs
  const [isLoading, setIsLoading] = React.useState(true)

  const wrappedOnDismiss = useCallback(() => {
    onDismiss()
  }, [onDismiss])

  React.useEffect(() => {
    setTimeout(() => setIsLoading(false), 3000)
  }, [])

  return (
    <Modal isOpen={isOpen} onDismiss={wrappedOnDismiss} maxHeight={90}>
      <ContentWrapper>
        <RowBetween>
          <ThemedText.MediumHeader style={{ margin: 'auto' }}>
            Space Racers - {spaceRaceTokenIDs?.length}
          </ThemedText.MediumHeader>
          <CloseIcon onClick={wrappedOnDismiss} />
        </RowBetween>
        <StyledFormControl>
          <Styles />
          {isLoaded && !isLoading ? (
            <>
              <Grid container rowSpacing={1} columnSpacing={{ xs: 1 }}>
                {spaceRaceTokenIDs.map((tokenID) => {
                  return (
                    <Grid item md={6} sm={6} xs={12} key={tokenID}>
                      <NFTItem
                        collection={spaceRaceCollection}
                        tokenID={tokenID}
                        handleChangeNFt={() => {
                          return
                        }}
                        nftList={selectedNFTList}
                        maxSelectionCount={100000}
                      ></NFTItem>
                    </Grid>
                  )
                })}
              </Grid>
            </>
          ) : (
            <Loader />
          )}
        </StyledFormControl>
      </ContentWrapper>
    </Modal>
  )
}
