import Grid from '@mui/material/Grid'
import { useWeb3React } from '@web3-react/core'
import { AutoColumn } from 'components/Column'
import Loader from 'components/Loader'
import { CustomConnectButtonCard, CustomWrongNetworkCard } from 'components/SpaceRace/Helper'
import SpaceRaceAstronauts from 'components/SpaceRace/SpaceRaceAstronauts'
import SpaceRaceNFTs from 'components/SpaceRace/SpaceRaceNFTs'
import Stat from 'components/SpaceRace/Stat'
import { SwitchLocaleLink } from 'components/SwitchLocaleLink'
import { isCustomSupportedChain } from 'constants/chains'
import styled from 'styled-components/macro'

const PageWrapper = styled(AutoColumn)`
  padding: 68px 8px 0px;
  max-width: 1570px;
  width: 100%;

  ${({ theme }) => theme.deprecated_mediaWidth.deprecated_upToMedium`
    max-width: 800px;
  `};

  ${({ theme }) => theme.deprecated_mediaWidth.deprecated_upToSmall`
    max-width: 500px;
  `};

  @media only screen and (max-width: ${({ theme }) => `${theme.breakpoint.md}px`}) {
    padding-top: 48px;
  }

  @media only screen and (max-width: ${({ theme }) => `${theme.breakpoint.sm}px`}) {
    padding-top: 20px;
  }
`

export default function SpaceRace() {
  const { chainId, account } = useWeb3React()

  if (!chainId) {
    return <Loader></Loader>
  } else if (!account) {
    return <CustomConnectButtonCard />
  } else if (!isCustomSupportedChain(chainId)) {
    return <CustomWrongNetworkCard />
  }

  return (
    <>
      <PageWrapper>
        <Grid container spacing={2} style={{ zIndex: '100' }}>
          <Grid item xs={12} sm={6} md={6} lg={4}>
            <SpaceRaceNFTs></SpaceRaceNFTs>
          </Grid>
          <Grid item xs={12} sm={6} md={6} lg={4}>
            <SpaceRaceAstronauts></SpaceRaceAstronauts>
          </Grid>
          <Grid item xs={12} sm={12} md={12} lg={4}>
            <Stat />
          </Grid>
        </Grid>
      </PageWrapper>
      <SwitchLocaleLink />
    </>
  )
}
