import { Trade } from '@uniswap/router-sdk'
import { Currency, CurrencyAmount, Percent, Token, TradeType } from '@uniswap/sdk-core'
import { useWeb3React } from '@web3-react/core'
import { useSingleCallResult } from 'lib/hooks/multicall'
import useSwapApproval from 'lib/hooks/swap/useSwapApproval'
import { ApprovalState, useApproval } from 'lib/hooks/useApproval'
import { useCallback, useMemo } from 'react'

import { useHasPendingApproval, useTransactionAdder } from '../state/transactions/hooks'
import { TransactionType } from '../state/transactions/types'
import { useERC721Contract } from './useContract'
export { ApprovalState } from 'lib/hooks/useApproval'

function useGetAndTrackApproval(getApproval: ReturnType<typeof useApproval>[1]) {
  const addTransaction = useTransactionAdder()
  return useCallback(() => {
    return getApproval().then((pending) => {
      if (pending) {
        const { response, tokenAddress, spenderAddress: spender } = pending
        addTransaction(response, { type: TransactionType.APPROVAL, tokenAddress, spender })
      }
    })
  }, [addTransaction, getApproval])
}

// returns a variable indicating the state of the approval and a function which approves if necessary or early returns
export function useApproveCallback(
  amountToApprove?: CurrencyAmount<Currency>,
  spender?: string
): [ApprovalState, () => Promise<void>] {
  const [approval, getApproval] = useApproval(amountToApprove, spender, useHasPendingApproval)
  return [approval, useGetAndTrackApproval(getApproval)]
}

export function useApproveCallbackFromTrade(
  trade: Trade<Currency, Currency, TradeType> | undefined,
  allowedSlippage: Percent
): [ApprovalState, () => Promise<void>] {
  const [approval, getApproval] = useSwapApproval(trade, allowedSlippage, useHasPendingApproval)
  return [approval, useGetAndTrackApproval(getApproval)]
}

export function useNFTApprovalStatus(nft: Token | undefined, contract: string | undefined): ApprovalState {
  const { account } = useWeb3React()
  const nftContract = useERC721Contract(nft?.address ?? undefined)
  const approvalRes = useSingleCallResult(nftContract ?? undefined, 'isApprovedForAll', [
    account ?? undefined,
    contract ?? undefined,
  ])
  const pendingApproval = useHasPendingApproval(nft, account)
  const approvalState: ApprovalState = useMemo(() => {
    if (pendingApproval) return ApprovalState.PENDING
    if (approvalRes?.result?.[0] === true) return ApprovalState.APPROVED
    if (approvalRes?.result?.[0] === false) return ApprovalState.NOT_APPROVED
    return ApprovalState.UNKNOWN
  }, [approvalRes, pendingApproval])
  return approvalState
}
