import { useERC721Uri, useERC1155Uri } from 'hooks/useENSAvatar'
import uriToHttp from 'lib/utils/uriToHttp'
import { useEffect, useMemo, useState } from 'react'

export function useMetadata(
  contractAddress: string | undefined,
  tokenID: string,
  protocol = 'erc721'
): { image?: string; loading: boolean } {
  const isERC721 = protocol === 'erc721'
  const isERC1155 = protocol === 'erc1155'
  const erc721 = useERC721Uri(isERC721 ? contractAddress : undefined, isERC721 ? tokenID : undefined, false)
  const erc1155 = useERC1155Uri(isERC1155 ? contractAddress : undefined, isERC1155 ? tokenID : undefined, false)
  const uri = erc721.uri || erc1155.uri
  const http = uri && uriToHttp(uri)[0]
  const [loading, setLoading] = useState(false)
  const [image, setImage] = useState(undefined)
  useEffect(() => {
    setImage(undefined)
    if (http) {
      setLoading(true)
      fetch(http)
        .then((res) => res.json())
        .then(({ image }) => {
          setImage(image)
        })
        .catch((e) => console.warn(e))
        .finally(() => {
          setLoading(false)
        })
    }
  }, [http])

  return useMemo(
    () => ({ image, loading: erc721.loading || erc1155.loading || loading }),
    [image, erc1155.loading, erc721.loading, loading]
  )
}
