/* eslint-disable react/jsx-key */
import Grid from '@mui/material/Grid'
import { useWeb3React } from '@web3-react/core'
import { ButtonEmphasis, ButtonSize } from 'components/Button'
import Loader from 'components/Loader'
import { RowBetween } from 'components/Row'
import { nativeOnChain } from 'constants/tokens'
import React, { useEffect } from 'react'
import { useTokenIDs } from 'state/spaceRace/portfolio'
import { useSpaceRaceMarsInfo } from 'state/spaceRace/spaceRaceMars'
import { ThemedText } from 'theme'

import BackToEarthModal from './BackToEarthModal'
import NFTItem from './NFTItem'
import { Button, ContentWrapper, StyledFormControl, StyleRowBetween, Styles } from './style'

// eslint-disable-next-line import/no-unused-modules
export default function SpaceRaceAstronauts() {
  const { chainId } = useWeb3React()

  const marsInfo = useSpaceRaceMarsInfo()
  const astronautsTokenIDs = useTokenIDs(marsInfo?.token)
  const [selectedAstronautsList, setSelectedAstronautsList] = React.useState<string[]>([])
  const [isDisabled, setIsDisabled] = React.useState<boolean>(true)
  const native = chainId ? nativeOnChain(chainId) : undefined
  const [isLoading, setIsLoading] = React.useState(true)
  const isLoaded = marsInfo && astronautsTokenIDs
  const [showBackToEarthModal, setShowBackToEarthModal] = React.useState(false)
  const [trigger, setTrigger] = React.useState(false)
  const totalReturnFee = marsInfo?.returnFee?.multiply(selectedAstronautsList.length)

  const maxSelectionCount = 10

  const buttonDisableCheck = () => {
    if (selectedAstronautsList.length === 0) {
      setIsDisabled(true)
    } else setIsDisabled(false)
  }

  const addNFT = (tokenID: string) => {
    const newList = selectedAstronautsList
    newList.push(tokenID)
    setSelectedAstronautsList(newList)
    buttonDisableCheck()
  }
  const removeNFT = (tokenID: string) => {
    const newList = selectedAstronautsList
    newList.forEach((element, index) => {
      if (element === tokenID) {
        newList.splice(index, 1)
      }
    })
    setSelectedAstronautsList(newList)
    buttonDisableCheck()
  }

  const handleChangeAstrounts = (tokenID: string) => {
    setTrigger(!trigger)
    if (selectedAstronautsList.length !== 0) {
      if (selectedAstronautsList.includes(tokenID)) {
        removeNFT(tokenID)
      } else {
        addNFT(tokenID)
      }
    } else {
      addNFT(tokenID)
    }
  }

  const clearNftList = () => {
    setSelectedAstronautsList([])
  }

  useEffect(() => {
    setTimeout(() => setIsLoading(false), 3000)
  }, [])

  return (
    <ContentWrapper>
      <RowBetween>
        <ThemedText.MediumHeader style={{ margin: 'auto' }}>
          Martians - {astronautsTokenIDs?.length}
        </ThemedText.MediumHeader>
      </RowBetween>
      <StyledFormControl>
        <Styles />
        {marsInfo?.token && isLoaded && !isLoading ? (
          <Grid container rowSpacing={1} columnSpacing={{ xs: 1 }}>
            {astronautsTokenIDs.map((tokenID) => {
              return (
                <Grid item md={6} sm={6} xs={12}>
                  <NFTItem
                    collection={marsInfo?.token}
                    tokenID={tokenID}
                    handleChangeNFt={handleChangeAstrounts}
                    nftList={selectedAstronautsList}
                    maxSelectionCount={maxSelectionCount}
                  ></NFTItem>
                </Grid>
              )
            })}
          </Grid>
        ) : (
          <Loader />
        )}
      </StyledFormControl>
      <StyleRowBetween>
        <RowBetween>
          <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>
            Num Selected:{' '}
          </ThemedText.DeprecatedWhite>
          <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>
            {selectedAstronautsList?.length}{' '}
          </ThemedText.DeprecatedWhite>
        </RowBetween>

        <RowBetween>
          <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>
            Total Return Fee:{' '}
          </ThemedText.DeprecatedWhite>
          <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>{`${totalReturnFee?.toFixed(4)} ${
            native?.symbol
          }`}</ThemedText.DeprecatedWhite>
        </RowBetween>

        <RowBetween>
          <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>
            Income:{' '}
          </ThemedText.DeprecatedWhite>
          <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>
            {marsInfo?.balanceOf && selectedAstronautsList?.length > 0
              ? marsInfo?.withdrawableDividend
                  ?.multiply(selectedAstronautsList?.length)
                  .divide(marsInfo?.balanceOf?.toString())
                  .toFixed(4)
              : 0}
            {` ${native?.symbol}`}
          </ThemedText.DeprecatedWhite>
        </RowBetween>
      </StyleRowBetween>
      <RowBetween style={{ flexDirection: 'row-reverse' }}>
        <Button
          style={{ background: 'linear-gradient(270deg, rgba(84,135,200,1) 0%, rgba(65,103,150,1) 38%)' }}
          onClick={() => setShowBackToEarthModal(true)}
          disabled={isDisabled}
          size={ButtonSize.medium}
          emphasis={ButtonEmphasis.medium}
        >
          Back to Earth 🚀
        </Button>
      </RowBetween>

      {marsInfo && (
        <BackToEarthModal
          isOpen={showBackToEarthModal}
          tokenIDs={selectedAstronautsList}
          onDismiss={() => setShowBackToEarthModal(false)}
          astronautsCollection={marsInfo?.token}
          clearNftList={clearNftList}
        />
      )}
    </ContentWrapper>
  )
}
