import Grid from '@mui/material/Grid'
import { Token } from '@uniswap/sdk-core'
import { SupportedChainId } from '@uniswap/sdk-core'
import { useWeb3React } from '@web3-react/core'
import { ButtonEmphasis, ButtonSize } from 'components/Button'
import Loader from 'components/Loader'
import { RowBetween } from 'components/Row'
import { TEST_SPACE_RACE_NFT } from 'constants/nftProjects'
import { nativeOnChain } from 'constants/tokens'
import React, { useEffect } from 'react'
import { useTokenIDs } from 'state/spaceRace/portfolio'
import { useSpaceRaceMarsInfo } from 'state/spaceRace/spaceRaceMars'
import { useSpaceRaceNFTInfo } from 'state/spaceRace/spaceRaceNFT'
import { ThemedText } from 'theme'

import NFTItem from './NFTItem'
import SendToSpaceModal from './SendToSpaceModal'
import { Button, ContentWrapper, StyledFormControl, StyleRowBetween, Styles } from './style'

const SPACE_RACE_NFT_COLLECTIONS: {
  [chainId: number]: Token
} = {
  [SupportedChainId.POLYGON_MUMBAI]: TEST_SPACE_RACE_NFT,
}

// eslint-disable-next-line import/no-unused-modules
export default function SpaceRaceNFTs() {
  const { chainId } = useWeb3React()
  const spaceRaceCollection = chainId ? SPACE_RACE_NFT_COLLECTIONS[chainId] : undefined
  const spaceRaceInfo = useSpaceRaceNFTInfo()
  const marsInfo = useSpaceRaceMarsInfo()
  const spaceRaceTokenIDs = useTokenIDs(spaceRaceCollection)
  const [selectedNFTList, setSelectedNFTList] = React.useState<string[]>([])
  const [isDisabled, setIsDisabled] = React.useState<boolean>(true)
  const native = chainId ? nativeOnChain(chainId) : undefined

  const teamGoLeft = marsInfo?.teamGoLimit && marsInfo?.teamGoCount ? marsInfo?.teamGoLimit - marsInfo?.teamGoCount : 0
  const totalGoFee = marsInfo?.goFee?.multiply(selectedNFTList.length)

  const isLoaded = spaceRaceCollection && spaceRaceInfo && spaceRaceTokenIDs
  const [showSendToSpaceModal, setShowSendToSpaceModal] = React.useState(false)
  const [trigger, setTrigger] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(true)

  const maxSelectionCount = 10
  const buttonDisableCheck = () => {
    if (selectedNFTList.length === 0) {
      setIsDisabled(true)
    } else setIsDisabled(false)
  }

  const addNFT = (tokenID: string) => {
    const newList = selectedNFTList
    newList.push(tokenID)
    setSelectedNFTList(newList)
    buttonDisableCheck()
  }
  const removeNFT = (tokenID: string) => {
    const newList = selectedNFTList
    newList.forEach((element, index) => {
      if (element === tokenID) {
        newList.splice(index, 1)
      }
    })
    setSelectedNFTList(newList)
    buttonDisableCheck()
  }

  const handleChangeNFt = (tokenID: string) => {
    setTrigger(!trigger)
    if (selectedNFTList.length !== 0) {
      if (selectedNFTList.includes(tokenID)) {
        removeNFT(tokenID)
      } else {
        addNFT(tokenID)
      }
    } else {
      addNFT(tokenID)
    }
  }

  const clearNftList = () => {
    setSelectedNFTList([])
  }

  useEffect(() => {
    setTimeout(() => setIsLoading(false), 3000)
  }, [])

  return (
    <ContentWrapper>
      <RowBetween>
        <ThemedText.MediumHeader style={{ margin: 'auto' }}>
          Space Racers - {spaceRaceTokenIDs?.length}
        </ThemedText.MediumHeader>
      </RowBetween>
      <StyledFormControl>
        <Styles />
        {isLoaded && !isLoading ? (
          <>
            <Grid container rowSpacing={1} columnSpacing={{ xs: 1 }}>
              {spaceRaceTokenIDs.map((tokenID) => {
                return (
                  <Grid item md={6} sm={6} xs={12} key={tokenID}>
                    <NFTItem
                      collection={spaceRaceCollection}
                      tokenID={tokenID}
                      handleChangeNFt={handleChangeNFt}
                      nftList={selectedNFTList}
                      maxSelectionCount={maxSelectionCount}
                    ></NFTItem>
                  </Grid>
                )
              })}
            </Grid>
          </>
        ) : (
          <Loader />
        )}
      </StyledFormControl>
      <StyleRowBetween>
        <RowBetween>
          <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>
            Num Selected:
          </ThemedText.DeprecatedWhite>
          <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>
            {selectedNFTList?.length}
          </ThemedText.DeprecatedWhite>
        </RowBetween>

        <RowBetween>
          <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>
            Total Go Fee:
          </ThemedText.DeprecatedWhite>
          <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>{`${totalGoFee?.toFixed(4)} ${
            native?.symbol
          }`}</ThemedText.DeprecatedWhite>
        </RowBetween>

        {spaceRaceInfo?.isTeamMember && (
          <RowBetween>
            <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>
              Team Go Left:
            </ThemedText.DeprecatedWhite>
            <ThemedText.DeprecatedWhite fontWeight={500} fontSize={16}>
              {teamGoLeft}
            </ThemedText.DeprecatedWhite>
          </RowBetween>
        )}
      </StyleRowBetween>
      <RowBetween style={{ flexDirection: 'row-reverse' }}>
        {marsInfo?.ticketPaused === true ? (
          <Button disabled={true} size={ButtonSize.medium} emphasis={ButtonEmphasis.medium}>
            Paused
          </Button>
        ) : (
          <Button
            onClick={() => setShowSendToSpaceModal(true)}
            disabled={isDisabled}
            size={ButtonSize.medium}
            emphasis={ButtonEmphasis.medium}
          >
            Send to Mars 🚀
          </Button>
        )}
      </RowBetween>
      {spaceRaceCollection && (
        <SendToSpaceModal
          isOpen={showSendToSpaceModal}
          tokenIDs={selectedNFTList}
          onDismiss={() => setShowSendToSpaceModal(false)}
          spaceRaceCollection={spaceRaceCollection}
          clearNftList={clearNftList}
        />
      )}
    </ContentWrapper>
  )
}
