/* eslint-disable react/jsx-key */
import Divider from '@mui/material/Divider'
import FormControlLabel from '@mui/material/FormControlLabel'
import ImageListItemBar from '@mui/material/ImageListItemBar'
import Skeleton from '@mui/material/Skeleton'
import { Token } from '@uniswap/sdk-core'
import React, { useState } from 'react'
import { useMetadata } from 'state/spaceRace/metadata'
import styled from 'styled-components/macro'

import { DataCard } from '../earn/styled'

const StyledImageListItemBar = styled(ImageListItemBar)`
  .MuiFormControlLabel-root {
    margin: 4px auto;
  }

  .MuiImageListItemBar-titleWrap {
    display: none;
  }
  .MuiImageListItemBar-actionIcon {
    background: transparent;
    width: 100%;
    text-align: center;
  }
`

const Styledimg = styled.img`
  width: 116px;
  height: 116px;
  margin: 6px auto;
  display: flex;
`
const PoolData = styled(DataCard)`
  background: none;
  padding: 1rem;
  z-index: 1;
  border-radius: 0;
  padding: 0.5rem;
  margin-top: 14px;
  padding-top: 1rem;
`

const StyleFormControlLabel = styled(FormControlLabel)`
  margin-right: 0 !important;
  .MuiTypography-root {
    font-family: 'Minecraftia';
    color: #ddd;
    font-size: 12px;
  }
`
const BorderDot = styled.div`
  box-shadow: 5px 10px 0 0 #000000;
  height: 5px;
  width: 5px;
  float: right;
  margin-top: -14px;
`
interface NFTItemProps {
  collection: Token | undefined
  tokenID: string
  handleChangeNFt(tokenID: string): any
  nftList: string[]
  maxSelectionCount: number
}

export default function NFTItem({ collection, tokenID, handleChangeNFt, nftList, maxSelectionCount }: NFTItemProps) {
  const metadata = useMetadata(collection?.address, tokenID)
  const [isSelect, setIsSelect] = useState(false)

  const clickData = () => {
    if (nftList.includes(tokenID)) {
      setIsSelect(!isSelect)
      handleChangeNFt(tokenID)
    } else {
      if (nftList.length !== maxSelectionCount) {
        setIsSelect(!isSelect)
        handleChangeNFt(tokenID)
      } else {
        setIsSelect(false)
      }
    }
  }

  return (
    <>
      {metadata.loading ? (
        <Skeleton sx={{ height: 234 }} animation="wave" variant="rectangular" />
      ) : metadata?.image ? (
        <>
          <PoolData onClick={() => clickData()}>
            <div key={tokenID} style={{ margin: 'auto' }}>
              <div
                style={{
                  width: '140px',
                  height: '180px',
                  opacity: nftList.length === maxSelectionCount && !nftList.includes(tokenID) ? '0.2' : '1',
                  border: isSelect ? '2px solid black' : '1px solid black',
                  background: isSelect ? '#121621' : 'none',
                  boxShadow: isSelect
                    ? 'inset -2px -4px #0006, inset 2px 2px #fff7'
                    : 'rgba(0,0,0,0.4) 0px 2px 4px, rgba(0,0,0,0.3) 0px 7px 13px -3px, rgba(0,0,0,0.2) 0px -3px 0px inset',
                }}
              >
                <BorderDot> </BorderDot>
                <Styledimg src={metadata.image} alt={tokenID} loading="lazy" />
                <Divider
                  style={{
                    margin: '1rem 1rem 0.2rem',
                    border: '1px solid black',
                  }}
                />
                <StyledImageListItemBar
                  position="below"
                  actionIcon={<StyleFormControlLabel control={<></>} value={tokenID} label={`#${tokenID}`} />}
                />
              </div>
            </div>
          </PoolData>
        </>
      ) : null}
    </>
  )
}
